.addRowHowerWrapper{
    position: absolute;
    display: flex;
    flex-direction: column;
    z-index: 1100;
    cursor: pointer;
   display: none;
   box-sizing: border-box;
}
.addSizeAbove,.addSizeBelow{
    flex: 50%;
}
.addSizeAbove{
  background-color: rgb(205, 235, 184);
}
.addSizeAbove:hover{
  background-color: rgb(169, 208, 142);
  color: #fff;
}
.addSizeBelow{
    background-color: rgb(109, 210, 247);
}
.addSizeBelow:hover{
    background-color: rgb(0, 176, 240);
    color: #fff;
}