@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;900&display=swap");
:root {
  /* --table-width: 90vw; */
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: poppins;
  text-align: center;
  font-size: 0.8rem;
}

table {
  table-layout: fixed;
  width: 90vw;
  /* height: 95vh; */
  margin: 1rem auto;
  max-width: 1500px;
  /* min-width: 800px; */
}
p{
	margin: 0;
	font-size: 14px;
}
.generate-chart-table {
  width: var(--table-width);
}

#table-cont {
  padding: 0 1rem;
}

table,
td,
th {
  border-collapse: collapse;
  border: 2px solid black;
}

td,
th {
  word-wrap: normal;
  padding: 0.5rem 5px;
  font-size: 1.01em;
  height: 30px;
}

.chart-descriptor {
  padding: 0 1px;
}

.chart-descriptor > input,
select {
  height: 100%;
}

#date-data-cell {
  font-size: 1.2em;
}

input {
  background-color: inherit;
  border-radius: 3px;
  width: 100%;
}

select {
  width: 100%;
}

.col-header-elem {
  font-size: 1em;
  padding: 0;
}

.row-elem,
.extra-pom-input,
#base-size {
  border: 0px solid black !important;
  font-weight: bold;
}

.row-elem::placeholder,
.extra-pom-input::placeholder,
#base-size::placeholder {
  font-weight: normal;
}

.row-elem:focus,
.extra-pom-input:focus {
  outline: none;
}

.data-row {
  line-height: 3em;
}

.data {
  padding: 3px !important;
}

.data > input,
.chart-descriptor > input {
  border: 2px solid rgba(211, 211, 211, 0.9);
  padding-left: 0.25rem;
}

.base-row > .measurement-cell {
  background-color: rgba(211, 211, 211, 0.9);
}

.hover-bg:hover {
  background-color: rgba(211, 211, 211, 0.9);
}

.disabled {
  cursor: not-allowed;
}

.select-bg {
  background-color: rgba(211, 211, 211, 0.9);
}
.focus-bg {
	background-color: rgb(231, 255, 215);

  }

.center-align {
  text-align: center;
}

.left-align {
  text-align: left;
}

#bar,
#pointer {
  display: inline-block;
  margin: 0;
  padding: 0;
}

#bar {
  width: 1rem;
  background-color: black;
  height: 0.25rem;
}

#pointer {
  width: 0;
  height: 0;
  border-top: 1rem solid transparent;
  border-left: 2rem solid black;
  border-bottom: 1rem solid transparent;
}

#nav {
  font-size: 1.7rem;
}

.pointer:hover {
  cursor: pointer;
}
.delete-clicked {
  background-color: rgba(255, 0, 0, 0.25);
}
.delete-mode:hover {
  background-color: rgba(255, 0, 0, 0.25);
  cursor: arrow;
}

.delete-btn-bg {
  background-color: rgb(248, 199, 167);
}

#scroll-cell {
  padding: 0;
  font-size: 1.7em;
}

#scroll-items-cont {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
}

#scroll-bar-cont {
  margin-left: 0;
  height: 80%;
  width: 78%;
  height: 80%;
  display: flex;
  align-items: center;
}

#scroll-bar {
  height: 100%;
  background-color: rgba(211, 211, 211, 0.9);
  border: 1px solid black;
  border-radius: 5px;
}

#prev-cols,
#next-cols {
  border-collapse: collapse;
  width: 100%;
  height: 100%;
  border: 2px solid black;
  margin: 0;
  font-weight: bold;
  text-shadow: 0px 0px 1px;
  border-radius: 0;
  display: table-cell;
}

#next-cols {
  margin-left: auto;
  margin-right: 2px;
  font-size: 3em;
}

#prev-cols {
  margin-right: auto;
  margin-left: 2px;
  font-size: 3em;
}

.press:hover {
  border: 1px groove black;
}

.hidden {
  display: none;
}

#print-btn {
  padding: 0.5em;
  margin-bottom: 2rem;
}

.html-ent {
  font-size: 2em;
  font-weight: bolder;
  margin-top: -7px;
  display: inline-block;
}
.html-ent-minus {
  font-size: 2em;
  font-weight: bolder;
  margin-top: -32px;
  display: inline-block;
  height: 7px;
}
.plus-icon {
  font-size: 1.5em !important;
}

.pom-gen-row {
  line-height: 2em;
  font-weight: bold;
}

/* input[type="date"]::-webkit-inner-spin-button, input[type="date"]::-webkit-calendar-picker-indicator {
	display: none;
	-webkit-appearance: none;
}

input[type="date"] {
	font-family: inherit;
	font-size: 0.75em;
} */
input {
  border: none;
  outline: none;
}
select {
  border: none;
  outline: none;
}

.gray-bg {
  background-color: rgba(211, 211, 211, 0.5);
}

.add-pom-bg {
  background-color: rgba(0, 162, 255, 0.5);
}

.bigger-font {
  font-size: 1.1em;
}

.gen-chart-arrow {
  font-size: 1.5em;
  vertical-align: middle;
  font-weight: bold;
}

.right-align {
  text-align: right;
}

.size-cell,
.measurement-cell,
.extra-pom-cell {
  padding: 0 !important;
}

.size-cell > input,
.measurement-cell > input,
.extra-pom-cell > input {
  height: 75%;
  width: 85%;
  padding-left: 2px;
}

.bold {
  font-weight: bold;
}

.normal-weight {
  font-weight: normal;
}

.row-manipulation-cell {
  font-size: 1.3em;
}
.row-manipulation-cell:hover p {
  color: #fff;
}

.measurement-option-row > td {
  height: 3.5em;
  padding: 0 !important;
}

.blue-bg {
  background-color: rgb(0, 176, 240);
}

.blue-hover:hover {
  background-color: rgb(0, 176, 240);
}
.blue-hover.clicked {
  background-color: rgb(0, 176, 240);
}
.blue-hover.clicked p{
	color: #fff;
}
.orangeish-hover:hover {
  background-color: rgb(244, 176, 132);
}
.orangeish-hover.clicked {
  background-color: rgb(244, 176, 132);
}
.orangeish-hover.clicked p{
	color: #fff;
}
.dark-blue-hover:hover {
  background-color: #8497b0;
}

.black-hover:hover {
  background-color: black;
  color: white;
}

.green-hover:hover {
  background-color: rgb(169, 208, 142);
}
.cream-hover:hover {
  background-color: rgb(255, 242, 204);
}
.oliveBrown-hover:hover {
  background-color: rgb(128, 96, 0);
  color: white;
}
.data-row-hover:hover {
  background-color: rgb(217, 217, 217);
}
.size-counter-cell {
  border: 2px solid black !important;
  position: relative;
}
.green-hover:hover {
  background-color: #92d050 !important;
}
#addRowDetect-wrapper {
  position: absolute;
  z-index: 10;
  width: 100%;
  height: 60px;
  top: 0;
  display: none;
}

tr {
  /* position: relative; */
}

#rowNoAbove,
#rowNoBelow {
  margin: 0;
  width: 100%;
  color: #fff;
  height: 29px;
  font-size: 15px;
  border: 1px solid transparent;
  border-radius: 0 !important;
  /* opacity: .9; */
}

#rowNoAbove {
  background-color: rgb(14, 228, 14);
}

#rowNoBelow {
  background-color: rgb(0, 176, 240);
}
tbody {
  /* position: relative; */
}
.color-white:hover {
  color: #fff;
}
.link {
  color: inherit;
  text-decoration: underline 2px;
}
.link:hover {
  color: inherit;
  text-decoration: underline 2px;
}
.restrict-click {
  pointer-events: none;
}
